import React from "react";
import {Redirect} from "react-router-dom";

class FallbackHandler extends React.Component {
    constructor(props) {
        super(props);
        this.state = {spotifyCallback: false, geniusCallback: false};
    }
    componentDidMount() {
        console.log(window.location);
        if (window.location.hash.includes('spotifycallback')) {
            this.setState({spotifyCallback: true});
        } if (window.location.pathname.includes('geniuscallback')) {
            this.setState({geniusCallback: true});
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
    }

    render() {
        return (
            <div>
                {this.state.spotifyCallback && <a href={`/spotify-lyrics#/spotifycallback${window.location.search}`}>Continue</a>}
                {this.state.geniusCallback && <a href={`/spotify-lyrics#/geniusCallback#${window.location.hash.substring(2)}`}>Continue</a>}
            </div>
        )
    }
}

export default FallbackHandler;